#section {
    padding-top: 75px;
    padding-bottom: 75px;
}

.section-heading {
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    text-align: center;
    margin-bottom: 15px;
    font-size: 2.5rem;
    font-weight: 700;
    color: white;
}

.grid-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: black;
    }

.card {
    width: 300px;
    height: auto;
    margin: 20px;
    background-color: #f2f2f2;
    border-radius: 10px;
    transition: opacity 1s ease-out;
}

.card-image {
    width: 100%;
    height: 100%;
    object-fit: fill;
    }
body {
    background: #000;
}

/* Responsive styles for screens smaller than 600px */
@media (max-width: 600px) {
    .grid-container {
        flex-direction: column; /* Stack cards vertically */
        align-items: center; /* Center align cards */
    }

    .card {
        width: 80%; /* Adjust card width for smaller screens */
    }
}