/* Hero.css */
.hero {
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    background-image: url('../assets/hero.jpg'); /* Adjust path as needed */
    background-size: cover;
    background-position: center;
    height: 100vh; /* Set height to full viewport height */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
  }

  /* Semi-transparent Overlay */
.hero::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: #ffff(0, 0, 0, 0.7); /* Black with 70% opacity (adjust as needed) */
    z-index: -1; /* Ensure the overlay is behind the content */
  }
  
  /* Text styles */
.hero-title {
    font-size: 3em; /* Adjust as needed */
    color: #3333;
    margin: 0;
    z-index: 1; /* Ensure text is above the overlay */
  }
  
  .hero-subtitle {
    font-size: 1.5em; /* Adjust as needed */
    color: #3333;
    z-index: 1; /* Ensure text is above the overlay */
  }

  @media (max-width: 768px) {
    .hero-title {
      font-size: 2em; /* Adjust for smaller screens */
    }
  
    .hero-subtitle {
      font-size: 1em; /* Adjust for smaller screens */
    }
  }
  